import { Routes, Route, useNavigate } from "react-router-dom";
import BottomNav from './BottomNav';
import HomePage from './HomePage';
import TestimonialPage from "./TestimonialPage";
import NoPage from './NoPage';
import OutletPage from "./OutletPage";
import ContactPage from "./ContactPage";
import MorePage from "./MorePage";

export default function RouteComponents() {
    const navigate = useNavigate();

    const handleBottomNavChange = (newIndex) => {
      switch(newIndex) {
        case 0:
          navigate("/", { replace: true });
          break;
        case 1:
          navigate("/testim", { replace: true });
          break;
        case 2:
          navigate("/contact", { replace: true });
          break;   
        case 3:
          navigate("/more", { replace: true });
          break;                    
        default:
          navigate("/not_found", { replace: true });
          break;
      }
    };
    
    return (
        <div>
            <Routes>
                <Route path="/" element={<OutletPage />}>
                <Route index element={<HomePage />} />
                <Route path="/testim" element={<TestimonialPage />} />
                <Route path="/contact" element={<ContactPage />} />
                <Route path="/more" element={<MorePage />} />
                <Route path="*" element={<NoPage />} />
                </Route>
            </Routes>
            <BottomNav handleIndexChange={handleBottomNavChange}/>
        </div>
      );          
}