import * as React from 'react';
import Paper from '@mui/material/Paper';
import BottomNavigation from '@mui/material/BottomNavigation';
import BottomNavigationAction from '@mui/material/BottomNavigationAction';
import DirectionsCarIcon from '@mui/icons-material/DirectionsCar';
import ThumbUpIcon from '@mui/icons-material/ThumbUp';
import PhoneIcon from '@mui/icons-material/Phone';
import MoreIcon from '@mui/icons-material/More';

export default function BottomNav({handleIndexChange}) {
  const [value, setValue] = React.useState(0);

  return (
    <Paper sx={{ position: 'fixed', bottom: 0, left: 0, right: 0 }} elevation={3}>
      <BottomNavigation
        showLabels
        value={value}
        onChange={(event, newValue) => {          
          setValue(newValue);
          handleIndexChange(newValue);
        }}
      >
        <BottomNavigationAction label="Home" icon={<DirectionsCarIcon />} />
        <BottomNavigationAction label="Testimonials" icon={<ThumbUpIcon />} />
        <BottomNavigationAction label="Contact Us" icon={<PhoneIcon />} />
        <BottomNavigationAction label="More" icon={<MoreIcon />} />
      </BottomNavigation>
    </Paper>
  );
}