import React from 'react';
import { Avatar, Grid } from '@mui/material';

const stringToColor = (string) => {
    let hash = 0;
    let i;

    /* eslint-disable no-bitwise */
    for (i = 0; i < string.length; i += 1) {
        hash = string.charCodeAt(i) + ((hash << 5) - hash);
    }

    let color = '#';

    for (i = 0; i < 3; i += 1) {
        const value = (hash >> (i * 8)) & 0xff;
        color += `00${value.toString(16)}`.slice(-2);
    }
    /* eslint-enable no-bitwise */

    return color;
};

const stringAvatar = (name) => {
    return {
        sx: {
            backgroundColor: stringToColor(name)
        },
        children: `${name.split(' ')[0][0]}${name.split(' ')[1][0]}`,
    };
};

const TestimonialCard = (props) => {
    var avatarProps = null;
    var avatarColor = stringToColor(props.name);
    if (props.image) {
        avatarProps = {
            src: props.image,
            alt: props.name
        };
    } else {
        avatarProps = stringAvatar(props.name)
    }

	return (
        <Grid container alignItems="center" justifyContent="center">
            <Grid item sx={{
                width: "90%",
                backgroundColor: '#ebebeb',
                boxShadow: '0px 0px 5px #c7c7c7',
                display: 'flex',
                flexDirection: 'column',
                alignItems: 'center',
                position: 'relative',
                marginTop: 10,
                paddingBottom: 5
            }}>
                <Grid item sx={{
                        height: 100,
                        width: 100,
                        position: 'absolute',
                        top: -50,
                        zIndex: 1,
                        borderRadius: '100%',
                        backgroundColor: 'white',
                        display: 'flex',
                        justifyContent: 'center',
                        alignItems: 'center'
                    }}
                >
                    <Avatar
                        {...avatarProps}
                        sx={{
                            width: '85%',
                            height: '85%',
                            borderRadius: '100%',
                            // backgroundColor: '#06232a',
                            backgroundColor: avatarColor,
                            display: 'flex',
                            justifyContent: 'center',
                            alignItems: 'center',
                            textAlign: 'center'
                        }}
                    />
                </Grid>
                <Grid item
                    xs={11}
                    sx={{
                        marginTop: 10,
                        flex: 1,
                        fontFamily: 'Helvetica, Arial, sans-serif',
                        textAlign: 'center',
                        '& > p': {
                            '&:before': {
                                content: `"\\201C"`,
                                fontSize: '2rem',
                                color: '#fda12e'
                            },
                            '&:after': {
                                content: `"\\201D"`,
                                color: '#fda12e',
                                fontSize: '2rem',
                                position: 'absolute',
                            },
                            margin: 0,
                            lineHeight: '20px'
                        }
                    }}
                >
                    <p>
                        {props.content}
                    </p>
                </Grid>
                <Grid item
                    sx={{
                        display: 'flex',
                        flexDirection: 'column',
                        alignItems: 'center',
                        textAlign: 'center',
                        margin: "15px 0px 10px 0px",
                        '& h3': {
                            margin: 0,
                            color: '#fda12e',
                            fontFamily: 'Helvetica, Arial, sans-serif',
                            fontSize: '1.3rem',
                        },
                        '& p': {
                            margin: 0,
                            fontFamily: 'serif',
                            fontStyle: 'italic',
                            fontSize: '0.9rem',
                        }
                    }}
                >
                    <Grid item>
                        <h3>{props.name}</h3>
                    </Grid>
                    <Grid item>
                        <p>{props.project}</p>
                    </Grid>
                </Grid>
            </Grid>
        </Grid>
    )
}

export default TestimonialCard;